import React, { Component } from "react";
// import NewsDetail from "./newsDetail";
import { connect } from "react-redux";
import {
  fetchNews,
  updateNews,
  fetchNewsBySource,
  translate
} from "../actions/newsAction";

import { newsResources } from "../config/newsSources";

export class News extends Component {
  //Use await instead of Promise

  async callNewsAwait() {
    try {
      const url =
        "https://newsapi.org/v2/top-headlines?country=us&apiKey=df47cddd4545452cb04ab5e1d030af8d";
      const response = await fetch(url);
      if (!response.ok) {
        throw Error(response.statusText);
      }
      const json = await response.json();
      console.log({ news: json.articles });
      this.props.updateNews(json.articles);
    } catch (error) {
      console.log("Error occurs!", this.state);
    }
  }

  handleUpdate = () => {
    this.props.fetchNews();
  };

  handleFetchBySource = source => {
    this.props.fetchNewsBySource(source);
  };

  handleTranslate = () => {
    this.props.translate();
  };

  handleMouseOver = desc => {
    alert(desc);
  };

  componentDidMount() {
    if (this.props.news.length > 0) {
      console.log("Get data from global store");
    } else {
      this.callNewsAwait();
    }
  }

  render() {
    console.log({ newsResources });
    return (
      <div>
        <div>
          <button
            className="float-right"
            onClick={() => this.handleFetchBySource("")}
          >
            ABC
          </button>
          <button
            className="float-right"
            onClick={() => this.handleFetchBySource("CNN")}
          >
            CNN
          </button>
          <button
            className="float-right"
            onClick={() => this.handleFetchBySource("bbc-news")}
          >
            BBC
          </button>
          <button
            className="float-right"
            onClick={() => this.handleFetchBySource("bbc-sport")}
          >
            BBC SPORT
          </button>
          <button
            className="float-right"
            onClick={() => this.handleFetchBySource("espn")}
          >
            ESPN
          </button>
          <hr />
        </div>

        <ul>
          {this.props.news.map((article, index) => {
            const desc = article.description;
            return (
              <div>
                {/* <li key={index} onMouseOver={() => this.handleMouseOver(desc)}> */}
                <li key={index}>
                  <a href={article.url}>{article.title}</a>
                </li>
                <p>{desc}</p>
              </div>
            );
          })}
        </ul>
      </div>
    );
  }
}

function mapStateToProps(globalState) {
  const temp = {
    news: globalState.news
  };
  return temp;
}

export default connect(
  mapStateToProps,
  { updateNews, fetchNews, fetchNewsBySource, translate }
)(News);
